.app-wrapper {
    display: grid;
    width: 1200px;
    margin: auto;
    grid-template-areas:
        "h h"
        "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 8fr;
    grid-gap: 10px;
}



.content {
    grid-area: c;
    background-color: cornflowerblue;
}

    .content img {
        max-width: 800px;
    }
